.social {
  display: flex;
  flex-wrap: wrap;
  padding: 0;

  list-style-type: none;

  gap: 15px 24px;
}

.social__link {
  display: flex;
  align-items: center;
  width: 28px;
  height: 42px;

  &:active {
    opacity: 0.3;
  }

  &::before {
    content: "";

    display: block;
    width: 28px;
    height: 22px;

    background-color: $color-dim-gray;

    mask-position: center;
    mask-repeat: no-repeat;
  }
}

.social__link--vk::before {
  mask-image: url("../img/svg/vkontakte.svg");
}

.social__link--youtube::before {
  mask-image: url("../img/svg/youtube.svg");
}

.social__link--telegram::before {
  mask-image: url("../img/svg/telegram.svg");
}

.social__link:hover::before {
  background-color: $color-apple;
}
